/**
 * Modern Application Entry Point
 * Handles ES6 module loading and initialization with Vite support
 */

// Import core modules
import eventBus from './js/core/eventBus.js';
import { createLogger } from './js/core/logger.js';
import eventDelegation from './js/core/event-delegation.js';

// Import UI modules
import modalManager from './js/ui/modals.js';

const logger = createLogger('App:Main');

class Application {
    constructor() {
        this.initialized = false;
        this.modules = new Map();
    }

    /**
     * Initialize the application
     */
    async initialize() {
        if (this.initialized) {
            logger.warn('Application already initialized');
            return;
        }

        logger.info('üöÄ Initializing modern application...');

        try {
            // Initialize core systems first
            await this.initializeCore();

            // Initialize UI systems
            await this.initializeUI();

            // Initialize feature modules
            await this.initializeFeatures();

            // Set up global error handling
            this.setupErrorHandling();

            this.initialized = true;
            logger.info('‚úÖ Modern application initialized successfully');

            // Log module status
            this.logModuleStatus();

        } catch (error) {
            logger.error('‚ùå Failed to initialize application:', error);
            throw error;
        }
    }

    /**
     * Initialize core systems
     */
    async initializeCore() {
        logger.info('Initializing core systems...');

        // Initialize event bus
        if (eventBus && typeof eventBus.initialize === 'function') {
            eventBus.initialize();
            this.modules.set('eventBus', eventBus);
        }

        // Initialize event delegation
        if (eventDelegation && typeof eventDelegation.initialize === 'function') {
            eventDelegation.initialize();
            this.modules.set('eventDelegation', eventDelegation);
        }

        logger.info('‚úÖ Core systems initialized');
    }

    /**
     * Initialize UI systems
     */
    async initializeUI() {
        logger.info('Initializing UI systems...');

        // Initialize modal manager
        if (modalManager && typeof modalManager.initialize === 'function') {
            modalManager.initialize();
            this.modules.set('modalManager', modalManager);
        }

        // Initialize tab management
        if (window.TabManagementModule && typeof window.TabManagementModule.initialize === 'function') {
            window.TabManagementModule.initialize();
            this.modules.set('tabManagement', window.TabManagementModule);
        }

        // Initialize form handling
        if (window.FormHandlingModule && typeof window.FormHandlingModule.setupManualGrading === 'function') {
            window.FormHandlingModule.setupManualGrading();
            this.modules.set('formHandling', window.FormHandlingModule);
        }

        logger.info('‚úÖ UI systems initialized');
    }

    /**
     * Initialize feature modules
     */
    async initializeFeatures() {
        logger.info('Initializing feature modules...');

        // Initialize grading display system
        if (window.GradingDisplayModule && typeof window.GradingDisplayModule.initializeGradingDisplay === 'function') {
            window.GradingDisplayModule.initializeGradingDisplay();
            this.modules.set('gradingDisplay', window.GradingDisplayModule);
        }

        // Initialize highlighting system
        if (window.HighlightingModule) {
            this.modules.set('highlighting', window.HighlightingModule);
        }

        // Initialize text selection
        if (window.TextSelectionModule) {
            this.modules.set('textSelection', window.TextSelectionModule);
        }

        // Initialize category selection
        if (window.CategorySelectionModule) {
            this.modules.set('categorySelection', window.CategorySelectionModule);
        }

        // Initialize essay editing
        if (window.EssayEditingModule) {
            this.modules.set('essayEditing', window.EssayEditingModule);
        }

        // Initialize profiles module
        if (window.ProfilesModule) {
            this.modules.set('profiles', window.ProfilesModule);
        }

        // Initialize PDF export
        if (window.PDFExportModule) {
            this.modules.set('pdfExport', window.PDFExportModule);
        }

        logger.info('‚úÖ Feature modules initialized');
    }

    /**
     * Set up global error handling
     */
    setupErrorHandling() {
        // Global error handler
        window.addEventListener('error', (event) => {
            logger.error('Global error caught:', {
                message: event.message,
                filename: event.filename,
                lineno: event.lineno,
                colno: event.colno,
                error: event.error
            });
        });

        // Promise rejection handler
        window.addEventListener('unhandledrejection', (event) => {
            logger.error('Unhandled promise rejection:', event.reason);
        });

        // Module loading error handler
        window.addEventListener('error', (event) => {
            if (event.target.tagName === 'SCRIPT') {
                logger.error('Script loading error:', {
                    src: event.target.src,
                    error: event
                });
            }
        }, true);

        logger.info('‚úÖ Global error handling set up');
    }

    /**
     * Log the status of all modules
     */
    logModuleStatus() {
        const status = {};
        this.modules.forEach((module, name) => {
            status[name] = !!module;
        });

        // Add legacy modules
        status.legacy = {
            UIInteractionsModule: !!window.UIInteractionsModule,
            EssayManagementModule: !!window.EssayManagementModule,
            BatchProcessingModule: !!window.BatchProcessingModule,
            DisplayUtilsModule: !!window.DisplayUtilsModule
        };

        logger.info('üìä Module status:', status);
    }

    /**
     * Get a module by name
     * @param {string} name - Module name
     * @returns {object|null} Module instance or null
     */
    getModule(name) {
        return this.modules.get(name) || null;
    }

    /**
     * Check if application is initialized
     * @returns {boolean} True if initialized
     */
    isInitialized() {
        return this.initialized;
    }

    /**
     * Destroy the application
     */
    destroy() {
        logger.info('üîÑ Destroying application...');

        // Destroy modules in reverse order
        this.modules.forEach((module, name) => {
            if (module && typeof module.destroy === 'function') {
                try {
                    module.destroy();
                    logger.debug(`Destroyed module: ${name}`);
                } catch (error) {
                    logger.error(`Error destroying module ${name}:`, error);
                }
            }
        });

        this.modules.clear();
        this.initialized = false;

        logger.info('‚úÖ Application destroyed');
    }
}

// Create application instance
const app = new Application();

// Initialize when DOM is ready
if (document.readyState === 'loading') {
    document.addEventListener('DOMContentLoaded', () => {
        app.initialize();
    });
} else {
    // DOM is already ready
    app.initialize();
}

// Export for global access and debugging
window.App = app;

export default app;